<template>
    
    <section class="pb-8" id="contact">
      <v-container fluid>
        <v-row align="center" justify="center">
          <v-col cols="10">
            <v-row justify="center">
              <v-col cols="12" sm="5">
                <h1 class="font-weight-light display-1">Contate-nos</h1>
                <h2 class="font-weight-light mt-3">Porque para nós o seu contacto é importante!!!!!</h2>
                <h2 class="font-weight-light mt-3">Horário: 10:00 - 20:00 GMT</h2>
                <h2 class="font-weight-light mt-3">Localização - Portugal</h2>
                <h2 class="font-weight-light mt-3">Email - hi@digitalwish.pt</h2>
              </v-col>
              <v-col cols="12" sm="7">
                <form role="form" method="POST" @submit.prevent="sendEmail">                
                <!--NAME -->
                <v-col cols="1">
                <label>Nome:</label>
                <input required type="text" name="name" placeholder="Name" v-model="nameMsg"/>  </v-col>                
                <!--EMAIL -->
                <v-col cols="1">
                <label>Mail:</label>
                <input type="email" name="_replyto" placeholder="abc@abc.com" v-model="emailMsg"/></v-col>
                <!--MESSAGE-->
                <v-col cols="1">
                <label>Mensagem:</label> 
                <textarea name="message" rows="4" cols="100" placeholder="Message" v-model="messageMsg"></textarea></v-col>

                <!--SUBMIT BUTTON-->
                <v-col cols="1">
                <input type="submit" :disabled="!nameMsg || !emailMsg || !messageMsg"/> </v-col>
                <!--LOADING MESSAGE-->               
                </form>
                <div v-if="loadingTxt">
                <p class="mb-8 text-primary">A entregar o seu post...</p>
                <div v-if="box"></div>
                 
              </div>
              
            <!-- <vue-recaptcha sitekey="6LftEnggAAAAAMB5PTcjAATr9rLuYOUXs87XLa_T"></vue-recaptcha>-->
              
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </section>
  </template>
  
  <style scoped>
  #contact {
    background-color: #f4f7f5;
  }
  label {
    font-size: 20px;
}
   
  </style>
  
  <script>
    import { VueRecaptcha } from 'vue-recaptcha';
    import axios from 'axios';
    
    export default {
    components: { VueRecaptcha },
    
    data() {    
    return {      
          nameMsg: '',      
          emailMsg: '',      
          messageMsg: '',      
          loadingTxt: false,
          box:false,
          };  
    },
    
    methods: {    
    sendEmail() {      
    this.loadingTxt= true;  
    this.box = window.alert("Mesagem enviada com sucesso!") ;    
    axios.post('https://formspree.io/f/xwkzlzwr',{
       name: this.nameMsg,          
       from: this.emailMsg,          
       _subject: `${this.nameMsg} | Friendly Message from Digitalwish Page`,
       message: this.messageMsg,},
    ).then((response) => {
       this.nameMsg = '';
       this.emailMsg = '';
       this.messageMsg = '';
       this.loadingTxt = false;  
       this.$router.push({ path: '/success' }); 
         
    })
    },
  },
  
}
    </script>